import React from "react";
import PropTypes from "prop-types";
import { Link } from "react-router-dom";
import { mealPreferences } from " ../../../tools/mockData";
import DropdownButton from "react-bootstrap/DropdownButton";
import Dropdown from "react-bootstrap/Dropdown";

const objectToArraySelector = (obj) => {
  return Object.keys(obj).map((key) => obj[key]);
};
const menuOption = (passenger, options, onSelect, optionName) => (
  <DropdownButton
    id="purchase-button"
    variant="secondary"
    title="Update"
    size="sm"
  >
    {objectToArraySelector(options).map(
      (option) =>
        option &&
        option.value && (
          <Dropdown.Item
            key={option.id}
            onSelect={() => onSelect(passenger.id, option.id, optionName)}
          >
            {option.value}
          </Dropdown.Item>
        )
    )}
  </DropdownButton>
);

const PassengerList = ({
  passengers,
  flightName,
  inFlight = false,
  onSelect,
  onSave,
  ancillaries = { id: null },
  specialMeals = {},
  purchases = {},
  saving = false,
}) => {
  return (
    <>
      <table className="table">
        <thead>
          <tr>
            <th>PNR</th>
            <th>Name</th>
            <th>Ancillary Service</th>
            <th>Seat No.</th>
            {!inFlight ? <th>WheelChair</th> : <th>Meal Preference</th>}
            {!inFlight ? <th>Infants</th> : <th>In-Flight purchase</th>}
            {inFlight && <th>Special Meals</th>}
            {inFlight && <th></th>}
          </tr>
        </thead>
        <tbody>
          {passengers.map((passenger) => {
            const ancillaryPresent =
              passenger.ancillary.length > 0 &&
              ancillaries.id &&
              passenger.ancillary.filter((a) => ancillaries.value[a]).length >
                0;
            const mealsPresent =
              passenger.specialMeals.length > 0 &&
              passenger.specialMeals.filter((a) => specialMeals[a]).length > 0;
            const purchasesPresent =
              passenger.purchase.length > 0 &&
              passenger.purchase.filter((a) => purchases[a]).length > 0;
            return (
              <tr key={passenger.id}>
                <td>
                  {inFlight ? (
                    passenger.pnr
                  ) : (
                    <Link
                      to={
                        "/staff/passenger/" + flightName + "/" + passenger.pnr
                      }
                    >
                      {passenger.pnr}
                    </Link>
                  )}
                </td>
                <td>{passenger.name}</td>
                <td>
                  {ancillaryPresent
                    ? passenger.ancillary.map(
                        (a) =>
                          ancillaries.value[a] && (
                            <div key={a}>{ancillaries.value[a].value}</div>
                          )
                      )
                    : "NIL"}
                  {inFlight &&
                    menuOption(
                      passenger,
                      ancillaries.value,
                      onSelect,
                      "ancillary"
                    )}
                </td>
                <td>
                  {passenger.seat ? passenger.seat : "--"}
                  {!inFlight && (
                    <Link
                      className="btn btn-primary btn-sm"
                      style={{ marginLeft: 10 }}
                      to={
                        "/staff/passenger/" + flightName + "/" + passenger.pnr
                      }
                    >
                      {passenger.seat ? "update" : "CheckIn"}
                    </Link>
                  )}
                </td>
                {!inFlight && (
                  <td>
                    {passenger.wheelChair && true === passenger.wheelChair
                      ? "YES"
                      : "NO"}
                  </td>
                )}
                {!inFlight && <td>{passenger.infants}</td>}
                {inFlight && (
                  <td>
                    {passenger.mealPreference
                      ? mealPreferences[passenger.mealPreference].value
                      : "No Meal"}
                    {menuOption(
                      passenger,
                      mealPreferences,
                      onSelect,
                      "mealPreference"
                    )}
                  </td>
                )}
                {inFlight && (
                  <td>
                    {purchasesPresent
                      ? passenger.purchase.map(
                          (a) =>
                            purchases[a] && (
                              <div key={a}>{purchases[a].value}</div>
                            )
                        )
                      : "NIL"}
                    {menuOption(passenger, purchases, onSelect, "purchase")}
                  </td>
                )}
                {inFlight && (
                  <td>
                    {mealsPresent
                      ? passenger.specialMeals.map(
                          (a) =>
                            specialMeals[a] && (
                              <div key={a}>{specialMeals[a].value}</div>
                            )
                        )
                      : "NIL"}
                    {menuOption(
                      passenger,
                      specialMeals,
                      onSelect,
                      "specialMeals"
                    )}
                  </td>
                )}
                {inFlight && (
                  <td>
                    <button
                      id={passenger.id}
                      className="btn btn-primary btn-sm"
                      onClick={onSave}
                    >
                      {saving ? "saving.." : "save"}
                    </button>
                  </td>
                )}
              </tr>
            );
          })}
        </tbody>
      </table>
      {inFlight && (
        <p className="information">
          **Select from the options to add or remove the item
        </p>
      )}
    </>
  );
};

PassengerList.propTypes = {
  passengers: PropTypes.array.isRequired,
  flightName: PropTypes.string.isRequired,
  inFlight: PropTypes.bool.isRequired,
  onSelect: PropTypes.func.isRequired,
  onSave: PropTypes.func.isRequired,
  ancillaries: PropTypes.object.isRequired,
  specialMeals: PropTypes.object,
  purchases: PropTypes.object,
  saving: PropTypes.bool,
};

export default PassengerList;
