import { handleResponse, handleError } from "./apiUtils";
const baseUrl = process.env.API_URL + "/ancillaries/";

export function getAncillaries() {
  return fetch(baseUrl).then(handleResponse).catch(handleError);
}

export function saveAncillary(ancillary) {
  return fetch(baseUrl + (ancillary.id || ""), {
    method: ancillary.id ? "PUT" : "POST", // POST for create, PUT to update when id already exists.
    headers: { "content-type": "application/json" },
    body: JSON.stringify(ancillary),
  })
    .then(handleResponse)
    .catch(handleError);
}
