/* eslint-disable no-unused-vars */
import * as types from "./actionTypes";
import * as ancillaryApi from "../../api/ancillaryApi";

export function loadAncillariesSuccess(ancillaries) {
  return { type: types.LOAD_ANCILLARIES_SUCCESS, ancillaries };
}

export function createAncillarySuccess(ancillary) {
  return { type: types.CREATE_ANCILLARY_SUCCESS, ancillary };
}

export function updateAncillarySuccess(ancillary) {
  return { type: types.UPDATE_ANCILLARY_SUCCESS, ancillary };
}

export function loadAncillaries() {
  return function (dispatch) {
    // dispatch(beginApiCall());
    return ancillaryApi
      .getAncillaries()
      .then((ancillaries) => {
        dispatch(loadAncillariesSuccess(ancillaries));
      })
      .catch((error) => {
        // dispatch(apiCallError(error));
        throw error;
      });
  };
}

export function saveAncillary(ancillary) {
  return function (dispatch, getState) {
    // dispatch(beginApiCall());
    return ancillaryApi
      .saveAncillary(ancillary)
      .then((savedAncillary) => {
        ancillary.id
          ? dispatch(updateAncillarySuccess(savedAncillary))
          : dispatch(createAncillarySuccess(savedAncillary));
      })
      .catch((error) => {
        // dispatch(apiCallError(error));
        throw error;
      });
  };
}
