import React from "react";
import PassengerList from "./PassengerList";
import { connect } from "react-redux";
import PropTypes from "prop-types";
import { bindActionCreators } from "redux";
import * as flightActions from "../../redux/actions/flightActions";
import * as passengerActions from "../../redux/actions/passengerActions";
import { newFlight, newAncillaries } from "../../../tools/mockData";
import { Link } from "react-router-dom";
import DropdownButton from "react-bootstrap/DropdownButton";
import Dropdown from "react-bootstrap/Dropdown";
import * as ancillaryActions from "../../redux/actions/ancillaryActions";
import Spinner from "../common/Spinner";

class PassengersPage extends React.Component {
  state = {
    passengers: [],
    saving: false,
  };
  componentDidMount() {
    console.log("component did mount" + JSON.stringify(this.props.passengers));
    const {
      loadFlights,
      flights,
      passengers,
      loadPassengers,
      flightAncillaries,
      loadAncillaries,
    } = this.props;
    if (flights.length === 0) {
      loadFlights().catch((error) => alert("Loading flights failed " + error));
    }
    if (!flightAncillaries.id) {
      loadAncillaries().catch((error) =>
        alert("Loading ancillaries failed " + error)
      );
    }
    if (passengers.length === 0) {
      loadPassengers().catch((error) =>
        alert("Loading passengers failed " + error)
      );
    } else {
      this.setState({ passengers });
    }
  }
  componentDidUpdate(prevProps) {
    if (this.props.passengers !== prevProps.passengers) {
      console.log("passengerpage:componentDidUpdate");
      this.setState({ passengers: this.props.passengers });
    }
  }

  handleFilter = (value) => {
    if (value === "Infants") {
      this.setState({
        passengers: this.props.passengers.filter((a) => a.infants > 0),
      });
    } else if (value === "WheelChair") {
      this.setState({
        passengers: this.props.passengers.filter((a) => a.wheelChair),
      });
    } else if (value === "CheckedIn") {
      this.setState({
        passengers: this.props.passengers.filter(
          (a) => a.seat && a.status !== "available"
        ),
      });
    } else {
      this.setState({ passengers: this.props.passengers });
    }
  };

  handleSelect = (passengerId, optionId, optionName) => {
    this.setState((prevState) => ({
      passengers: prevState.passengers.map((a) =>
        a.id === passengerId
          ? optionName === "mealPreference"
            ? { ...a, [optionName]: optionId }
            : a[optionName].filter((anc) => anc === optionId).length === 0
            ? { ...a, [optionName]: [...a[optionName], optionId] }
            : {
                ...a,
                [optionName]: a[optionName].filter((p) => p !== optionId),
              }
          : a
      ),
    }));
  };

  handleSave = (event) => {
    event.preventDefault();
    this.setState({ saving: true });
    const { id } = event.target;
    const passenger = this.state.passengers.find(
      (passenger) => passenger.id === parseInt(id, 10)
    );
    this.props
      .savePassenger(passenger)
      .then(() => this.setState({ saving: false }))
      .catch((error) => {
        this.setState({ saving: false });
        console.log("failed to save passenger details" + error);
      });
  };
  render() {
    return (
      <>
        <h2>Passengers</h2>

        {this.props.flights.length === 0 || !this.props.passengers ? (
          <Spinner></Spinner>
        ) : (
          <>
            <div>
              <Link
                style={{ marginBottom: 20 }}
                to={
                  this.props.inFlight
                    ? "/staff/in-flight/" + this.props.name
                    : "/staff/flights"
                }
                className="btn btn-primary"
              >
                Back
              </Link>
              <DropdownButton
                style={{ float: "right" }}
                id="filter-button"
                variant="info"
                title="Filter"
              >
                <Dropdown.Item onSelect={() => this.handleFilter("CheckedIn")}>
                  CheckedIn
                </Dropdown.Item>
                <Dropdown.Item onSelect={() => this.handleFilter("WheelChair")}>
                  WheelChair
                </Dropdown.Item>
                <Dropdown.Item onSelect={() => this.handleFilter("Infants")}>
                  Infants
                </Dropdown.Item>
                <Dropdown.Item onSelect={() => this.handleFilter("")}>
                  Clear filters...
                </Dropdown.Item>
              </DropdownButton>
            </div>
            <PassengerList
              passengers={this.state.passengers}
              flightName={this.props.name}
              inFlight={this.props.inFlight}
              onSelect={this.handleSelect}
              onSave={this.handleSave}
              ancillaries={this.props.flightAncillaries}
              purchases={this.props.flight.purchases}
              specialMeals={this.props.flight.specialMeals}
              saving={this.state.saving}
            ></PassengerList>
          </>
        )}
      </>
    );
  }
}

PassengersPage.propTypes = {
  flights: PropTypes.array.isRequired,
  passengers: PropTypes.array.isRequired,
  loadFlights: PropTypes.func.isRequired,
  loadPassengers: PropTypes.func.isRequired,
  loadAncillaries: PropTypes.func.isRequired,
  savePassenger: PropTypes.func.isRequired,
  name: PropTypes.string.isRequired,
  inFlight: PropTypes.bool.isRequired,
  flight: PropTypes.object.isRequired,
  flightAncillaries: PropTypes.object.isRequired,
};

export function getFlightByName(flights, name) {
  return flights.find((a) => a.name === name) || null;
}

function mapStateToProps(state, ownProps) {
  const name = ownProps.match.params.name;
  const path = ownProps.match.path;
  const inFlight = path === "/staff/in-flight/passengers/:name";
  const flight =
    name && state.flights.length > 0
      ? getFlightByName(state.flights, name)
      : newFlight;
  const passengers =
    flight && state.passengers.length > 0
      ? state.passengers.filter((passenger) => passenger.flightId === flight.id)
      : [];
  const flightAncillaries =
    state.ancillaries.length > 0 && flight.id
      ? state.ancillaries.find((a) => a.id === flight.ancillariesId)
      : newAncillaries;
  return {
    name,
    flight,
    inFlight,
    flightAncillaries,
    flights: state.flights,
    passengers,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    loadFlights: bindActionCreators(flightActions.loadFlights, dispatch),
    loadPassengers: bindActionCreators(
      passengerActions.loadPassengers,
      dispatch
    ),
    savePassenger: bindActionCreators(passengerActions.savePassenger, dispatch),
    loadAncillaries: bindActionCreators(
      ancillaryActions.loadAncillaries,
      dispatch
    ),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(PassengersPage);
